.Reasons {
    padding: 0 2rem;
    display: flex;
    gap: 2rem;
}

.left-r {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}

.left-r>img {
    object-fit: cover;
}

.left-r>:nth-child(1) {
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
}

.left-r>:nth-child(2) {
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}

.left-r>:nth-child(3) {
    width: 14rem;
    grid-column: 2 / 3;
    grid-row: 2;

}

.left-r>:nth-child(4) {
    width: 10rem;
    grid-row: 2;
    grid-column: 3 / 4;

}



.right-r {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-transform: uppercase;
    font-weight: 700;
}

.right-r>:nth-child(1) {
    text-transform: uppercase;
    color: #FFC020;
    font-weight: 700;

}

.right-r>:nth-child(2) {
    color: #fff;
    font-weight: 700;
    font-size: 3rem;
}

.details-r {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.details-r>div {
    display: flex;
    font-size: 1rem;
    gap: 1rem;
    align-items: center;
    color: #fff;
}

.details-r>div>img {
    height: 2rem;
    width: 2rem;
}

.right-r>:nth-child(4) {
    color: #9c9c9c;
    font-weight: normal;
}

.partners {
    display: flex;
    gap: 1rem;
}

.partners>img {
    width: 2.5rem;
}

@media screen and (max-width: 768px) {
    .Reasons{
        flex-direction: column;
    }
    .left-r{
        grid-auto-rows: auto;
        overflow: hidden;
    }
    .left-r > :nth-child(1){
        width: 7rem;
        height: 17rem;
    }
    .left-r > :nth-child(2){
        width: 15rem;
        height: 10rem;
    }
    .left-r > :nth-child(3){
        width: 7rem;
    }
    .left-r > :nth-child(4){
        width: 7rem;
        height: 6rem;
    }

    .right-r > :nth-child(2){
        font-size: 2rem;
    }

    .details-r > div{
        font-size: 0.9rem;
        font-weight: 500;
    }

    .details-r > div >   img{
        width: 1.5rem;
        height: 1.5rem;
    }
}